import React, { useState } from 'react';
import {IconButton, Box, Grid, List, ListItem, ListItemButton} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { StyledAppBar, TitleTypography, ButtonTypography, StyledToolbar } from "./header.styled"
import DiamondIcon from '@mui/icons-material/Diamond';

import { Link, animateScroll as scroll } from "react-scroll";

const MainNavBar = (props) => {
    const [isDropDownMenuOpen, setDropDownMenuOpen] = useState(false);

    const toggleDropDownMenu = () => {
        setDropDownMenuOpen(!isDropDownMenuOpen)
    }

    if (props.isMobile) {

        // Mobile View
        return (
            <Box component="nav">
                <StyledAppBar>
                    <Grid container direction="column">
                        <StyledToolbar variant="dense">
                            <Link to="/#home" spy={true} smooth={true} offset={50} duration={500}>
                                <TitleTypography variant="h6" noWrap component="a">
                                    <DiamondIcon/> Anthony Huang
                                </TitleTypography>
                            </Link>

                            <div>
                                <IconButton size="large" edge="start" color="inherit" aria-label="menu" onClick={toggleDropDownMenu}>
                                    <MenuIcon />
                                </IconButton>
                            </div>

                            
                        </StyledToolbar>

                        
                        {
                            isDropDownMenuOpen ? 
                            <List>
                                <ListItem sx={{color:"text.primary"}}>
                                    <Link to="/#about" spy={true} smooth={true} offset={50} duration={500}>
                                        <ListItemButton onClick={toggleDropDownMenu}>
                                            <ButtonTypography variant="h6" noWrap>
                                                About
                                            </ButtonTypography>
                                        </ListItemButton>
                                    </Link>
                                </ListItem>
                                <ListItem sx={{color:"text.primary"}}>
                                    <Link to="/projects" spy={true} smooth={true} offset={50} duration={500}>
                                        <ListItemButton onClick={toggleDropDownMenu}>
                                            <ButtonTypography variant="h6" noWrap>
                                                Projects
                                            </ButtonTypography>
                                        </ListItemButton>
                                    </Link>
                                </ListItem>
                            </List>
                            :
                            null
                        }
                    </Grid>
                </StyledAppBar>
            </Box>
        )
    }
    else{

        // Window View
        return(
            <Box component="nav">
                <StyledAppBar>
                    <StyledToolbar variant="dense">
                        <Link to="/#home" spy={true} smooth={true} offset={50} duration={500}>
                            <TitleTypography variant="h6" noWrap>
                                <DiamondIcon/> Anthony Huang
                            </TitleTypography>
                        </Link>

                        <div>
                            <Link to="/#about" spy={true} smooth={true} offset={50} duration={500}>
                                <ButtonTypography variant="h6" noWrap>
                                    About
                                </ButtonTypography>
                            </Link>

                            <Link to="/project" spy={true} smooth={true} offset={50} duration={500}>
                                <ButtonTypography variant="h6" noWrap component="a">
                                    Projects
                                </ButtonTypography>
                            </Link>
                        </div>
                    </StyledToolbar>
                </StyledAppBar>
            </Box>
        )
    }
};

export default MainNavBar;